import Vue from 'vue'
import VueRouter from 'vue-router'

// tabbar
const Index = () => import('../components/index.vue')
const List = () => import('../components/list.vue')
const Register = () => import('../components/register.vue')


// 1.
Vue.use(VueRouter)
// const routes = []
// const Home = () => import()
// 2.
const routes = [
    {
        path:'',
        redirect:'/index',
    },
    {
        path:'/index',
        component:Index,
    },
    {
        path:'/list',
        component:List,
    },
    {
        path:'/register',
        component:Register,
    },
    
]
const router = new VueRouter({
    routes,
    mode:'history',
})

// 3.
export default router


